<template>
  <v-app id="app" dark>
    <v-content id="v-content">
      <v-card id="window" color="primary" class="shake-animation">
        <div id="nav">
          <v-toolbar flat color="success">
            <v-toolbar-title>
              $: {{headerTextCurrent}}
              <transition name="cursor-fade">
                <span v-if="typingText">_</span>
              </transition>
            </v-toolbar-title>
            <v-spacer></v-spacer>
            <v-toolbar-items class="hidden-sm-and-down">
              <v-btn flat to="/">Home</v-btn>
              <v-btn flat to="/preview">Preview</v-btn>
              <v-btn flat to="/todo">Todo</v-btn>
            </v-toolbar-items>
            <v-menu id="burger-menu" left nudge-bottom=5 offset-y transition="slide-y-transition">
              <template v-slot:activator="{ on }">
                <v-btn dark icon v-on="on" class="hidden-md-and-up">
                  <v-icon>menu</v-icon>
                </v-btn>
              </template>
              <v-list id="burger-menu-list">
                <v-list-tile>
                  <v-btn flat to="/">Home</v-btn>
                </v-list-tile>
                <v-list-tile>
                  <v-btn flat to="/preview">Preview</v-btn>
                </v-list-tile>
                <v-list-tile>
                  <v-btn flat to="/todo">Todo</v-btn>
                </v-list-tile>
              </v-list>
            </v-menu>
            <!-- <v-toolbar-side-icon></v-toolbar-side-icon> -->
            <v-btn flat icon color="error" @click="shakeAnimationF">
              <v-icon>close</v-icon>
            </v-btn>
          </v-toolbar>
        </div>
        <router-view/>
        <v-footer height="23" class="pa-2 caption">
          <v-spacer></v-spacer>
          <div>&copy; {{ new Date().getFullYear() }}</div>
        </v-footer>
      </v-card>
    </v-content>
  </v-app>
</template>

<script>
import { setInterval, clearInterval, setTimeout } from "timers";
export default {
    name: "App",
    methods: {
        shakeAnimationF() {
            var animation_elements = [document.getElementById("window")];
            for (let anim_elem of animation_elements) {
                anim_elem.classList.toggle("shake-animation-active");
                setTimeout(() => {
                    anim_elem.classList.remove("shake-animation-active");
                }, 300);
            }
        },
        displayTitle() {
            // reset interval and string
            clearInterval(this.intervallID);
            this.typingText = true;
            this.headerTextCurrent = "";
            this.headerText = this.$route.name;
            var maxLen = this.headerText.length;
            var delay = 200 / maxLen;
            // using es6 shorthand function declaration ovverides the this var to local so the parent this will be overriden
            var self = this;
            this.intervallID = setInterval(() => {
                var curLen = self.headerTextCurrent.length;
                self.headerTextCurrent += self.headerText[curLen];

                if (self.headerTextCurrent.length >= maxLen) {
                    clearInterval(self.intervallID);
                    setTimeout(() => {
                        self.typingText = false;
                    }, 500);
                }
            }, delay);
        }
    },
    data() {
        return {
            headerText: this.$route.name,
            headerTextCurrent: "",
            intervallID: null,
            typingText: true
        };
    },
    mounted() {
        this.displayTitle();
    },
    watch: {
        $route: function() {
            this.displayTitle();
        }
    }
};
</script>


<style lang="scss">
html {
    /* own scrollbar */
    /* scrollbar firefox */
    scrollbar-color: rgb(110, 110, 110) rgb(1, 1, 1);
}

.cursor-fade-leave-active {
    transition: all 0.8s ease;
}

.cursor-fade-enter,
.cursor-fade-leave-to {
    opacity: 0;
}

// #burger-menu {
//   padding-top: 60px
// }
#burger-menu-list {

}

#app {
    // font-family: "Avenir", Helvetica, Arial, sans-serif;
    font-family: "Source Code Pro", monospace !important;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    // line-height: 1rem;
    // text-align: center;
    // color: #2c3e50;
    background-color: #1e1e1e;
    background-image: url(data:image/png;base64,);
    background-origin: padding-box;
    background-position: left top;
    background-clip: border-box;
    background-repeat: repeat;
    background-attachment: scroll;
    background-size: auto;
}

#window {
    margin-left: 10%;
    margin-right: 10%;
    margin-top: 5%;
    margin-bottom: 5%;
    -webkit-box-shadow: 0px 0px 64px 3px rgba(0, 0, 0, 0.75);
    box-shadow: 0px 0px 64px 0px rgba(0, 0, 0, 0.75);
}

.v-toolbar__content {
    height: 45px !important;
}

.shake-animation-active {
    // animation: shake 0.3s;
    animation: shake 5s;
    // animation-iteration-count: 0.3s;
    animation-iteration-count: 5s;
}

@keyframes shake {
    0% {
        transform: translate(1px, 1px) rotate(0deg);
    }
    10% {
        transform: translate(-1px, -2px) rotate(-1deg);
    }
    20% {
        transform: translate(-3px, 0px) rotate(1deg);
    }
    30% {
        transform: translate(3px, 2px) rotate(0deg);
    }
    40% {
        transform: translate(1px, -1px) rotate(1deg);
    }
    50% {
        transform: translate(-1px, 2px) rotate(-1deg);
    }
    60% {
        transform: translate(-3px, 1px) rotate(0deg);
    }
    70% {
        transform: translate(3px, 1px) rotate(-1deg);
    }
    80% {
        transform: translate(-1px, -1px) rotate(1deg);
    }
    90% {
        transform: translate(1px, 2px) rotate(0deg);
    }
    100% {
        transform: translate(1px, -2px) rotate(-1deg);
    }
}

/* own scrollbar */
/* Note: The -webkit-scrollbar is not supported by Firefox or IE and Edge. */
::-webkit-scrollbar {
    /* width */
    width: 12px;
}
::-webkit-scrollbar-track {
    /* Track */
    box-shadow: inset 0 0 5px grey;
    border-radius: 8px;
}
::-webkit-scrollbar-thumb {
    /* Handle */
    background: rgb(110, 110, 110);
    border-radius: 10px;
}
::-webkit-scrollbar-thumb:hover {
    /* Handle on hover */
    background: rgb(110, 110, 110);
}
::-webkit-scrollbar-track-piece {
    /* not handle on */
    background: rgb(1, 1, 1);
}
</style>

